<template>
    <div class="xwbox">
        <navs>
            <div class="dh" slot="a">
                <ul>
                    <li v-for="(i,$index) in $store.state.nav" :key="$index" :class="index==$index?'active':''">
                        <router-link :to="i.h">
                            {{i.t}}
                        </router-link>
                        <span></span> 
                    </li>
                </ul> 
            </div>
        </navs>
        <div class="xw_img">
            <img src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNS85MjBhZGMwYzUzZTg4ZDYxNjQ5YTQzNjAyZjkxZDQ3NC05MC53ZWJw.webp" alt="">
        </div>
        <div class="xw_child">
            <div class="xw_n">
                <div v-for="(i,$index) in type" :key="i" class="el-icon-caret-right" @click="typr_arr(i,$index)" :class="tyind==$index?'active':''">
                    {{i}}
                </div>
            </div>
            <div class="xw_c">
                <div v-for="(i,$index) in arr" :key="$index">
                    <div class="xw_c_l">
                        <div class="c_l_tit">
                            <router-link :to="'/xwxq'+i.id">
                                {{i.tit}}
                            </router-link>
                            
                        </div>
                        <div class="c_l_t">
                            {{i.t}}
                        </div>
                        <div class="c_l_btn">
                            <router-link :to="'/xwxq'+i.id">
                                阅读正文<span class="el-icon-right"></span>
                            </router-link>
                            
                        </div>
                    </div>
                    <div class="c_r">
                        <img :src="i.img" alt="">
                    </div>
                </div>
            </div>
        </div>
        <footers></footers>
    </div>
</template>
<script>
export default {
    data () {
        return {
            index:4,
            arr:[],
            tyind:0
        }
    },
    methods: {
        typr_arr(type,index){
            this.tyind = index
            this.arr = []
            for(let i=0;i<this.$store.state.xw.length;i++){
                if(type == this.$store.state.xw[i].type){
                    this.arr.push(this.$store.state.xw[i])
                }
                if(type == '全部新闻'){
                    this.arr.push(this.$store.state.xw[i])
                }
            }
            console.log(this.arr)
        }
    },
    computed: {
        type(){
            let arr = ['全部新闻']
            for(let i=0;i<this.$store.state.xw.length;i++){
                if(arr.indexOf(this.$store.state.xw[i].type) == -1){
                    arr.push(this.$store.state.xw[i].type)
                }
            }
            return arr
        },
    },
    created () {
        this.arr = this.$store.state.xw
    }
}
</script>
<style scoped>
    .xw_img{
        width: 100%;
    }
    .xw_img>img{
        width: 100%;
    }
    .xw_child{
        width: 1200px;
        padding: 70px 0px;
        display: flex;
        margin: 0 auto;
    }
    .xw_n{
        width: 200px;
        border-top: 1px solid #90af6b;
        padding: 30px 0px;
    }
    .xw_n>div{
        width: 200px;
        font-size: 14px;
        color: #888888;
        line-height: 43px;
        border-bottom: 1px solid #eaeaea;
        text-indent: 1em;
        cursor: pointer;
    }
    .xw_n>div:last-of-type{
        border: none;
    }
    .xw_n>div:hover{
        color: #90af6b;
    }
    .xw_n>.active{
        color: #90af6b;
    }
    .xw_c{
        padding: 0px 40px;
        display: flex;
        flex-wrap: wrap;
    }
    .xw_c_l{
        width: 650px;
    }
    .xw_c>div{
        border-bottom: 1px solid #eaeaea;
    }
    .xw_c>div:hover .c_l_tit>a{
        color: #90af6b;
    }
    .xw_c>div>div{
        /* display: ; */
        float: left;
    }
    .c_r{
        width: 200px;
        height: 130px;
        overflow: hidden;
        margin: 20px;
    }
    .xw_c>div:hover .c_r img{
        transition: 1s;
        transform: scale(1.1);
    }
    .c_r>img{
        width: 200px;
    }
    .c_l_tit>a{
        text-decoration: none;
        font-size: 18px;
        line-height: 50px;
        color: #000000;
    }
    .c_l_t{
        color: #999;
        font-size: 13px;
        line-height: 20px;
        padding-bottom: 20px;
    }
    .c_l_btn{
        padding: 20px 0px;
    }
    .c_l_btn>a{
        font-size: 14px;
        color: #90af6b;
        text-decoration: none;
    }
</style>
