import navs from '../components/nav.vue'
import xc from '../components/xc.vue'
import l from '../components/l.vue'
import footer from '../components/footer.vue'
import tit from '../components/tit.vue'
import tu from '../components/tu.vue'
import seach from '../components/seach.vue'
let publics = {
    install(Vue){
        Vue.component('navs',navs),
        Vue.component('xc',xc),
        Vue.component('l',l),
        Vue.component('footers',footer),
        Vue.component('tit',tit),
        Vue.component('tu',tu),
        Vue.component('tan',seach)
    }
}

export default publics