<template>
    <div class="xcbox" ref="hz">
        <div class="xc_tit">
            <div>新茶上市</div>
            <span>Tea Leave</span>
        </div>
        <div class="xc_con">
            <div v-for="(i,$index) in 6" :key="$index">
                <router-link :to="'/cyxq'+$store.state.cy[i].m">
                <div class="xc_con_img">
                    <img :src="$store.state.cy[i].img" alt="">
                </div>
                <div class="xc_t">
                    {{$store.state.cy[i].m}}
                </div>
                <div class="jd">
                    <span></span>
                </div>
                <div class="xc_j">
                    {{$store.state.cy[i].j}}
                </div>
                </router-link>
            </div>
        </div>
        <div class="xc_btn">
                <router-link to="/cy" class="el-icon-right">
                    <!-- <i ></i> -->
                </router-link>
        </div>
        
    </div>
</template>
<script>

export default {
    data () {
        return {

        }
    },
    created () {
        
    },
    mounted () {
        // fun(){}
            let h = document.documentElement.offsetHeight
            let w = document.documentElement.clientHeight
            let s = document.documentElement.scrollTop
            let a = this.$refs.hz.offsetTop+this.$refs.hz.childNodes[1].offsetTop
            if(h-w-s < h-a){
                for(let i=0;i<this.$refs.hz.childNodes[1].childNodes.length;i++){
                    this.$refs.hz.childNodes[1].childNodes[i].style.opacity = '1'
                }
            }
        document.onscroll = () => {
                let h = document.documentElement.offsetHeight
                let w = document.documentElement.clientHeight
                let s = document.documentElement.scrollTop
                if(this.$refs.hz){
                    let a = this.$refs.hz.offsetTop+this.$refs.hz.childNodes[1].offsetTop
                    if(h-w-s < h-a-300){
                        for(let i=0;i<this.$refs.hz.childNodes[1].childNodes.length;i++){
                            this.$refs.hz.childNodes[1].childNodes[i].style.opacity = '1'
                        }
                    }
                }
                
        }
    }
}
</script>

<style scoped>
    .xcbox{
        background-color: #f7f7f7;
        position: relative;
        padding: 50px 0px;
    }
    .xc_tit{
        padding: 40px 0px;
        width: 1300px;
        margin: 0 auto;
        text-align: center;
    }
    .xc_tit>div{
        font-size: 24px;
        color: #232323;
    }
    .xc_tit>span{
        color: #999999;
        font-size: 13px;
    }
    .xc_con{
        max-width: 1300px;
        margin: 0 auto;
        text-align: center;
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 50px;
    }
    .xc_con>div{
        flex: 1;
        width: 400px;
        padding: 20px;
        transition: 0.5s;
        opacity: 0;
        margin: 0px 15px;
    }
    .xc_con>div>a{
        text-decoration: none;
    }
    .xc_con>div:hover .xc_t{
        transition: 0.5s;
        color: #519f10;
    }
    .xc_con_img{
        width: 360px;
        overflow: hidden;
        margin: 0 auto;
    }
    .xc_con>div:hover .xc_con_img img{
        transition: 1s;
        transform: scale(1.1);
    }
    .xc_con_img>img{
        width: 100%;
    }
    .xc_t{
        padding-bottom: 15px;
        font-size: 16px;
        color: #555555;
    }
    .xc_j{
        padding-top: 15px;
        color: #555555;
        font-size: 16px;
    }
    .jd{
        width: 350px;
        height: 1px;
        background-color: #ddd;
        margin: 0 auto;
        position: relative;
    }
    .xc_con>div:hover .jd span{
        transition: 1s;
        width: 350px;
    }   
    .jd span{
        width: 0px;
        background-color: #519f10;
        height: 1px;
        position: absolute;
        top: 0;
        left: 0;
    }
    .xc_btn>a{
        display: block;
        text-decoration: none;
        width: 84px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        font-size: 30px;
        color: #fff;
        background-color: #90Af68;
        margin: 0 auto;
    }

</style>