<script>
import {mapActions} from 'vuex'
export default {
    data () {
        return {
            arr:[],
            index:999,
            xgarr:[],
            num:0
        }
    },
    created () {
        document.documentElement.scrollTop = 0
        for(let i=0;i<this.$store.state.cy.length;i++){
            if(this.$route.params.t == this.$store.state.cy[i].m){
                this.arr = this.$store.state.cy[i]
            }
        }
        this.xgarr = []
        for(let i=0;i<this.$store.state.cy.length;i++){
            if(this.arr.type == this.$store.state.cy[i].type){
                this.xgarr.push(this.$store.state.cy[i])
            }
        }
        for(let i=0;i<this.xgarr.length;i++){
            if(this.arr.m == this.xgarr[i].m){
                this.xgarr.splice(i,1)
            }
        }
        for(let i=0;i<JSON.parse(localStorage.xgwc).length;i++){
            if(this.arr.m == JSON.parse(localStorage.xgwc)[i].m){
                this.num = JSON.parse(localStorage.xgwc)[i].num
            }
        }
    },
    mounted () {
        for(let i=0;i<JSON.parse(localStorage.xgwc).length;i++){
            if(this.arr.m == JSON.parse(localStorage.xgwc)[i].m){
                this.num = JSON.parse(localStorage.xgwc)[i].num
            }
        }
    },
    methods: {
      ...mapActions(['push','jia','jian'])
    },
}
</script>
