<template>
    <div class="gw">
        <navs>
            <div class="dh" slot="a">
                <ul>
                    <li v-for="(i,$index) in $store.state.nav" :key="$index" :class="index==$index?'active':''">
                        <router-link :to="i.h">
                            {{i.t}}
                        </router-link>
                        <span></span> 
                    </li>
                </ul> 
            </div>
        </navs>
        <div class="gwc">
            <div class="q" v-show="!type">
                <div>
                    您的购物车目前是空的。
                </div>
                <div>
                    <router-link to="/cy">
                        <button>返回商店</button>
                    </router-link>
                    
                </div>
            </div>
            <div v-show="type">
            <div class="gwc_l">
                <div class="top">
                    <div class="sp">商品</div>
                    <div class="jg">价格</div>
                    <div class="sl">数量</div>
                    <div class="hj">合计</div>
                </div>
                <div v-for="(i,$index) in arr" :key="$index" class="gwc_sj">
                    <div class="sp">
                        <div class="sc el-icon-circle-close" @click="sc(i.m)"></div>
                        <div class="s_img">
                            <img :src="i.img" alt="">
                        </div>
                        <div class="s_tit">
                            {{i.m}}
                        </div>
                    </div>
                    <div class="jg">{{i.j}}</div>
                    <div class="xan">
                        <input type="button" value="-" @click="[jian(i),numan(i)]">
                        <input type="button" :value="i.num">
                        <input type="button" value="+" @click="[jia(i),i.num++]">
                    </div>
                    <div class="hj">{{nun[$index]}}</div>
                </div>
            </div>
            <div class="gwc_r">
                <div class="gwc_r_tit">
                    购物车总计
                </div>
                <div class="xj">
                    <div class="xj_l">购物车小计</div>
                    <div class="xj_r">￥{{nun[0]}}</div>
                </div>
                <div class="yf">
                    <div class="xj_l">运费</div>
                    <div class="xj_r">免费送货</div>
                </div>
                <div class="zj">
                    <div class="xj_l">总计</div>
                    <div class="xj_r">￥{{nun[0]}}</div>
                </div>
                <div class="syt">
                    <button>前往收银台</button>
                </div>
                <div class="yhj">
                    <div class="yhj_tit">
                        优惠卷:
                    </div>
                    <input type="text" placeholder="优惠卷代码">
                    <input style="background-color: #90af6b;color: #fff;outline: none;border: none;" type="button" value="使用优惠卷">
                </div>
            </div>
        </div>
        </div>
        <footers></footers>
    </div>
</template>
<script>
import {mapActions} from 'vuex'
export default {
    data () {
        return {
            index:999,
            arr:[],
            nun:[],
            type:true,
            num:0
        }
    },
    methods: {
        ...mapActions(['push','jia','jian']),
        sc(m){
            for(let i=0;i<this.arr.length;i++){
                if(m == this.arr[i].m){
                    this.arr.splice(m,1)
                }
            }
            if(this.arr.length == 0){
                this.type = false
            }
            localStorage.xgwc = JSON.stringify(this.arr)
        },
        numan(j){
            j.num--
            // let type = true
                if(j.num == 0){
                    for(let i=0;i<this.arr.length;i++){
                        if(j.m == this.arr[i].m){
                            this.arr.splice(i,1)
                        }
                    }
                }
        }
    },
    created () {
        this.arr = this.$store.state.gwc
        if(localStorage.xgwc){
            this.arr = [...JSON.parse(localStorage.xgwc)]
        }
        let arr = []
        for(let i=0;i<this.arr.length;i++){
            arr = this.arr[i].j.split('')
            arr.splice(0,1)
            this.nun.push(parseInt(arr.join('')))
        }
        for(let i=0;i<JSON.parse(localStorage.xgwc).length;i++){
            if(this.arr.m == JSON.parse(localStorage.xgwc)[i].m){
                this.num = JSON.parse(localStorage.xgwc)[i].num
            }
        }
    },
    mounted () {
        if(this.arr.length == 0){
            this.type = false
        }
    },
    watch: {
        num(a){
            if(a<=0){
                this.num = 0
            }
        }
    }
}
</script>

<style scoped>
    .gwc{
        min-height: 500px;
        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        position: relative;
    }
    .gwc .q{
        position: absolute;
        /* text-align: center; */
        width:400px;
        height: 400px;
        margin:0 auto;
        font-size: 16px;
        color: #777777;
        top: 50%;
        margin-top: -100px;
        left: 50%;
        margin-left: -100px;
    }
    .gwc>.q>div>a>button{
        display: block;
        width: 142px;
        height: 42px;
        background-color: #519f10;
        color: #fff;
        outline: none;
        border: none;
        position: absolute;
        top: 100px;
        left: 10px;
    }
    .gwc{
        display: flex;
    }
    .gwc>div{
        padding: 50px 0px;
        width: 1200px;
        margin: 0 auto;
        display: flex;
    }
    .gwc_l>div>div{
        display: inline-block;
        padding: 5px;
    }
    .sp{
        margin-right: 380px;
    }
    .jg{
        margin-right: 60px;
    }
    .sl{
        margin-right: 100px;
    }
    .top{
        border-bottom: 3px solid #ddd;
    }
    .s_img{
        width: 90px;
        height: 90px;
        padding: 10px;
    }
    .s_img>img{
        width: 100%;
        height: 100%;
    }
    .gwc_sj{
        display: flex;
        height: 110px;
        line-height: 110px;
    }
    .gwc_sj .sp>div{
        line-height: 110px;
        float: left;
    }
    .gwc_sj .sp{
        margin-right: 150px;
    }
    .gwc_sj .sp>div{
        margin: 0 15px;
    }
    .gwc_r{
        margin-left: 50px;
        width: 400px;
        height: 400px;
    }
    .gwc_r_tit{
        line-height: 30px;
        border-bottom: 3px solid #ddd;
    }
    .xj{
        padding-top: 5px;
        height: 30px;
    }
    .yf{
        padding-top: 5px;
        height: 35px;
        border-bottom: 1px solid #ddd;
    }
    .xj_l{
        float: left;
        font-size: 17px;
        color: #777777;
    }
    .xj_r{
        float: right;
        font-size: 17px;
        color: #777777;
    }
    .zj{
        line-height: 50px;
        height: 50px;
        border-bottom: 3px solid #ddd;
    }
    .syt{
        padding: 20px 0px;
    }
    .syt>button{
        width: 100%;
        height: 42px;
        background-color: #519f10;
        color: #fff;
        text-align: center;
        line-height: 42px;
        outline: none;
        border: none;
        font-size: 16px;
    }
    .yhj_tit{
        font-size: 16px;
        color: #555;
        line-height: 30px;
        height: 30px;
        border-bottom: 3px solid #ddd;
    }
    .yhj>input{
        width: 100%;
        height: 40px;
        text-indent: 1em;
        margin-top: 10px;
    }
    .sc{
        cursor: pointer;
    }
    .xan{
        float: left;
        margin-left: 10px;
    }
    .xan>input{
        display: inline-block;
        width: 33px;
        height: 44px;
        background-color: #fff;
        outline: none;
        border: none;
        border: 1px solid #ddd;
        box-sizing: border-box;
    }
</style>