<template>
    <div class="cybox">
        <navs>
            <div class="dh" slot="a">
                <ul>
                    <li v-for="(i,$index) in $store.state.nav" :key="$index" :class="index==$index?'active':''">
                        <router-link :to="i.h">
                            {{i.t}}
                        </router-link>
                        <span></span> 
                    </li>
                </ul> 
            </div>
        </navs>
        <div class="cy_top">
            <div class="cy_h">
                茶叶
            </div>
            <div class="ctype">
                <div v-for="(i,$index) in type" :key="$index" @click="ctype(i,$index)" :class="typeindex==$index?'active':''">
                    {{i}}
                </div>
            </div>
        </div>
        <div class="cy_con">
            <div v-for="(i,$index) in arr" :key="$index">
                <div class="cy_con_img">
                    <router-link :to="'/cyxq'+i.m">
                        <img :src="i.img" alt="">
                    </router-link>
                </div>
                <div class="cy_con_tit">
                    <router-link to="">
                        {{i.m}}
                    </router-link>
                </div>
                <div class="jd">
                    <span></span>
                </div>
                <div class="cy_jg">
                    {{i.j}}
                </div>
                <div class="cy_btn">
                    <router-link to="" class="el-icon-right">
                    
                    </router-link>
                </div>
            </div>
        </div>
        <div class="cy_con_an" v-show="typeindex==0">
            <div class="qian" v-show="ymin!=0" @click="qian">第一页</div>
            <div class="shang" v-show="ymin!=0" @click="shang">上一页</div>
            <div v-for="(i,$index) in num" :key="i" @click="fy($index)" :class="ymin==$index?'active':''">
                {{i}}
            </div>
            <div class="xia" v-show="ymin!=1" @click="xia">下一页</div>
            <div class="hou" v-show="ymin!=1" @click="hou">最后一页</div>
        </div>
        <footers></footers>
    </div>
</template>
<script>
export default {
    data () {
        return {
            index:2,
            typeindex:0,
            arr:[],
            num:0,
            ymin:0,
            typea:false
        }
    },
    computed: {
        type(){
            let arr = ['全部']
            for(let i=0;i<this.$store.state.cy.length;i++){
                if(arr.indexOf(this.$store.state.cy[i].type) == -1){
                    arr.push(this.$store.state.cy[i].type)
                }
            }
            return arr
        }
    },
    created () {
        for(let i=0;i<this.$store.state.cy.length;i++){
            if(this.typeindex == 0){
                this.arr.push(this.$store.state.cy[i])
            }
        }
        
    },
    methods: {
        ctype(type,index){
            this.typeindex = index
            this.arr = []
            for(let i=0;i<this.$store.state.cy.length;i++){
                if(type == this.$store.state.cy[i].type){
                    this.arr.push(this.$store.state.cy[i])
                }
                if(type == '全部'){
                    this.arr.push(this.$store.state.cy[i])
                }
            }
        },
        fy(index){
            this.ymin=index
            this.arr = this.$store.state.cy.slice(index*10,index*10+10)
        },
        qian(){
            this.ymin = 0
            this.arr = this.$store.state.cy.slice(0,10)
        },
        hou(){
            this.ymin = 1
            this.arr = this.$store.state.cy.slice(10,20)
        },
        shang(){
            this.ymin--
            this.arr = this.$store.state.cy.slice(0,10)
        },
        xia(){
            this.ymin++
            this.arr = this.$store.state.cy.slice(10,20)
        }
    },
    mounted () {
        
    },
    watch: {
        arr(a){
            if(a.length/10 > 1){
                this.num = Math.ceil(a.length/10)
                this.arr = this.arr.slice(0,10)
                this.typea = true
            }
            if(a.length/10 < 1){
                this.typea = false
            }
            
        }
    }
}
</script>
<style scoped>
    .cy_top{
        min-height: 87px;
        background-color: #f4f4f480;
    }
    .cy_h{
        font-size: 24px;
        font-family: '微软雅黑';
        color: #333333;
        line-height: 70px;
        text-indent: 2em;
        float: left;
    }
    .ctype{
        float: right;
        margin-right: 50px;
        padding: 25px 0px;
    }
    .ctype>div{
        display: inline-block;
        padding: 8px 16px;
        color: #878787;
        font-size: 15px;
        margin: 0 6px;
    }
    .ctype>div:hover{
        color: #90af6b;
        border-bottom: 1px solid #90af6b;
    }
    .ctype>.active{
        color: #90af6b;
        border-bottom: 1px solid #90af6b;
    }
    .cy_con{
        width: 1450px;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        padding: 0px 20px;
        padding-top: 35px;
    }
    .cy_con>div{
        width: 260px;
        margin: 0px 15px;
    }
    .cy_con_img{
        max-width: 260px;
        height: 260px;
    }
    .cy_con_img>a>img{
        width: 100%;
    }
    .cy_con>div:hover .cy_con_tit>a{
        color: #519f10;
    }
    .cy_con_tit{
        padding-top: 15px;
        padding-bottom: 17px;
    }
    .cy_con_tit>a{
        font-size: 16px;
        color: #555555;
        text-decoration: none;
    }
    .cy_jg{
        font-size: 16px;
        color: #8e8e8e;
        line-height: 50px;
    }
    .jd{
        width: 260px;
        height: 1px;
        background: rgba(221,221,221,1);
        position: relative;
    }
    .jd span{
        width: 0px;
        background-color: #519f10;
        height: 1px;
        position: absolute;
        top: 0;
        left: 0;
    }
    .cy_con>div:hover .jd span{
        width: 260px;
        transition: 1s;
    }
    .cy_btn{
        padding-bottom: 110px;
    }
    .cy_btn>a{
        display: block;
        width: 31px;
        height: 28px;
        font-size: 13px;
        color: #d1d1d1;
        text-decoration: none;
        border: 1px solid rgba(221,221,221,1);
        line-height: 28px;
        text-align: center;
    }
    .cy_con>div:hover .cy_btn a{
        color: #519f10;
        border-color: #519f10;
    }
    .cy_con_an{
        height: 60px;
        text-align: center;
    }
    .cy_con_an>div{
        display: inline-block;
        padding: 2px 10px;
        margin: 0 10px;
        background-color: #eaeaea;
        border: 1px solid #ddd;
        color: #519f10;
        font-size: 14px;
    }
    .cy_con_an>.active{
        background-color: #fff;
    }
</style>

