import Vue from 'vue'
import index from '../views/index.vue'
import gy from '../views/gy.vue'
import cy from '../views/cy.vue'
import qt from '../views/qt.vue'
import xw from '../views/xw.vue'
import lx from '../views/lx.vue'
import jd from '../views/jd.vue'
import cyxq from '../views/cyxq.vue'
import xwxq from '../views/xwxq.vue'
import login from '../views/login.vue'
import gw from '../views/gw.vue'
import zf from '../views/zf.vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {path:'/',name:'index',component:index},
  {path:'/gy',name:'gy',component:gy},
  {path:'/cy',name:'cy',component:cy},
  {path:'/qt',name:'qt',component:qt},
  {path:'/xw',name:'xw',component:xw},
  {path:'/lx',name:'lx',component:lx},
  {path:'/jd',name:'jd',component:jd},
  {path:'/cyxq:t',name:'cyxq',component:cyxq},
  {path:'/xwxq:t',name:'xqxq',component:xwxq},
  {path:'/login',name:'login',component:login},
  {path:'/gw',name:'gw',component:gw},
  {path:'/zf',name:'zf',component:zf}
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
